      their full potential.\n              </p></div></div> <div class=\"p-15\"><p class=\"text-justify dancing-font has-text-weight-semibold p-b-10\">\n              Foundational pillars to my educational philosophy include the following:\n            </p> <div class=\"text-justify p-b-20 p-b-10-mob\"><span class=\"dancing-font has-text-weight-semibold\"> Learning To know:</span>\n              Understand children better, their emotions, their mindset, how they learn,\n              what environment is needed to learn.\n              AM I teacher? Am I good teacher? What qualities make me a good teacher?\n              What should my teaching style be? What information is relevant? Am I able\n              to teach the children a good perspective in life?\n            </div> <div class=\"text-justify p-b-10\"><span class=\"dancing-font has-text-weight-semibold\">Learning To Be:</span>\n              I would like to continuously learn and broaden my horizon about life,\n              and education and evolve as a person so that I can continuously grow and\n              be positive too. I would love to know myself better, think of qualities\n              and strength and my personality to work towards my self-development that\n              is above and beyond work. I want to be aware of my actions and reflect on\n              them myself. I would like to be an empathetic person who is happy and healthy,\n              thoughtful and creative, reflective, confident and independent. I would like to\n              be an intentional educator, an educator who works without any bias and inhibitions.\n            </div> <div class=\"text-justify p-b-10\"><span class=\"dancing-font has-text-weight-semibold\">Learning To Do:</span>\n              I would love to further understand the process of learning. Create and engage\n              students in mindful conversations, make it fun and arouse curiosity and use learnt\n              and application of learning. Be a better listener to my students and colleagues and\n              make creative lesson plans which a re resourceful and effective and connected to the\n              lives of the children and brings out the best in them.\n            </div> <div class=\"text-justify p-b-10\"><span class=\"dancing-font has-text-weight-semibold\">Learning To Live Together:</span>\n              Will I be remembered as one who brought about change in the education space?\n              I want to be surrounded with children, know them well and create a space that is\n              both imaginative and visible. I want to be aware of my actions on others and myself.\n              Work with children with special needs and empower them to learn freely. Have a personal\n              connect with them all.\n            </div> <div class=\"text-justify p-b-10\">\n              Just as a diamond needs to be polished from time to time, the pillars to my education\n              philosophy are constantly developing too. Through continuous improvement of ourselves,\n              we figure out how to achieve the ideal environment that we desire. The classroom thus\n              becomes a safe space where we can be reinvented; where we continuously involve with the\n              students and our teaching practice. I am passionate about teaching and helping students\n              come to love the act of learning. As an enthusiastic teacher, I am dedicated to enabling\n              young minds to grow and achieve.\n            </div></div></div></div></div></div>")])}
